import React, { useState } from "react";

const InternshipFilters = () => {
  const [filters, setFilters] = useState({
    profile: "",
    location: "",
    workFromHome: false,
    partTime: false,
    stipend: 0,
    startDate: "",
    duration: "",
    jobOffer: false,
  });

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFilters({
      ...filters,
      [name]: type === "checkbox" ? checked : value,
    });
  };

  return (
    <div className="max-w-lg mx-auto bg-white p-6 rounded-lg shadow-lg">
      <h2 className="text-xl font-bold mb-4">Filter Internships</h2>
      
      {/* Profile Role */}
      <label className="block mb-2">Profile Role</label>
      <select
        name="profile"
        value={filters.profile}
        onChange={handleChange}
        className="w-full p-2 border rounded"
      >
        <option value="">Select Profile</option>
        <option value="Marketing">Marketing</option>
        <option value="Engineering">Engineering</option>
      </select>

      {/* Location */}
      <label className="block mt-4 mb-2">Location</label>
      <select
        name="location"
        value={filters.location}
        onChange={handleChange}
        className="w-full p-2 border rounded"
      >
        <option value="">Select Location</option>
        <option value="Delhi">Delhi</option>
        <option value="Mumbai">Mumbai</option>
      </select>

      {/* Work from Home */}
      <div className="flex items-center mt-4">
        <input
          type="checkbox"
          name="workFromHome"
          checked={filters.workFromHome}
          onChange={handleChange}
          className="mr-2"
        />
        <label>Work from Home</label>
      </div>

      {/* Part-time */}
      <div className="flex items-center mt-2">
        <input
          type="checkbox"
          name="partTime"
          checked={filters.partTime}
          onChange={handleChange}
          className="mr-2"
        />
        <label>Part-time</label>
      </div>

      {/* Stipend */}
      <label className="block mt-4 mb-2">Desired Minimum Monthly Stipend (₹)</label>
      <input
        type="range"
        name="stipend"
        min="0"
        max="20000"
        step="500"
        value={filters.stipend}
        onChange={handleChange}
        className="w-full"
      />
      <span className="text-sm">₹{filters.stipend}</span>

      {/* Start Date */}
      <label className="block mt-4 mb-2">Starting from (or after)</label>
      <input
        type="date"
        name="startDate"
        value={filters.startDate}
        onChange={handleChange}
        className="w-full p-2 border rounded"
      />

      {/* Duration */}
      <label className="block mt-4 mb-2">Max. Duration (Months)</label>
      <select
        name="duration"
        value={filters.duration}
        onChange={handleChange}
        className="w-full p-2 border rounded"
      >
        <option value="">Select Duration</option>
        <option value="1">1 month</option>
        <option value="2">2 months</option>
        <option value="3">3 months</option>
        <option value="6">6 months</option>
        <option value="12">12 months</option>
      </select>

      {/* Job Offer */}
      <div className="flex items-center mt-4">
        <input
          type="checkbox"
          name="jobOffer"
          checked={filters.jobOffer}
          onChange={handleChange}
          className="mr-2"
        />
        <label>Internships with Job Offer</label>
      </div>
    </div>
  );
};

export default InternshipFilters;
